<template>
	<div id="noticeDiv">
		<div class="targ">
			<ol class="breadcrumb">
				<li>关于我</li>
			</ol>
		</div>
		<div class="mainBox">
			<div class="titHead">
				<el-row>
				  <el-col :xs="24" :sm="14" :md="14" :lg="8" :xl="8">
				  	<h1 class="page-title txt-color-blueDark"><i class="fa fa-bullhorn fa-fw"></i> 关于我 </h1>			  	
				  </el-col>
				  <el-col :xs="24" :sm="10" :md="10" :lg="16" :xl="16">
				  	<div class="titHeadTab">
					  	<el-button type="primary" size="small" @click="gotoDetail(1,0)">编辑个人信息</el-button>		
					</div>
				  </el-col>
				  
				</el-row>
			</div>
			<el-card class="box-card">
				<div slot="header" class="clearfix">
				    <span>基本信息</span>
				 </div>
			    <el-row class="row-bg" :gutter="20">
				  <el-col :span="3">
				  	<div class="headBox">
				  		<img :src="data.head" alt="" v-if="data.head">
				  		<img src="../../assets/images/head.png" alt="" v-else>
				  	</div>
				  </el-col>
				  <el-col :span="8">
				  	<div class="userInfoBox">
				  		<p>姓名：{{data.name}}</p>
				  		<p>学校：{{data.school}}</p>
				  		<p>专业：{{data.major}}</p>
				  		
				  	</div>
				  </el-col>
				  <el-col :span="8">
				  	<div class="userInfoBox">
				  		<p>职业：{{data.Occupation}}</p>
				  		<p>手机号：{{data.mobile}}</p>
				  		<p>邮箱：{{data.email}}</p>
				  		
				  	</div>
				  </el-col>
				</el-row>
			</el-card>
			<el-card class="box-card">
				<div slot="header" class="clearfix">
				    <span>自我介绍</span>
				 </div>
				{{data}}
			</el-card>
			<el-card class="box-card">
				<div slot="header" class="clearfix">
				    <span>技能</span>
				 </div>
				{{data}}
			</el-card>
			<el-card class="box-card">
				<div slot="header" class="clearfix">
				    <span>作品</span>
				 </div>
				{{data}}
			</el-card>
				
						
		</div>
	</div>
</template>
<script>
import api from '../../api'
	export default{
		data(){
			return {
		        
		        data:{}
				
				
			}
		},
		computed:{

		},
		created(){
			this.getInfo();

		},
		mounted(){
			
		},
		methods:{
	      getInfo(){
	      	var self=this;
	      	const loading=self.$loading({
	          lock: true,
	          text: '请求中……',
	          spinner: 'el-icon-loading',
	          background: 'rgba(0, 0, 0, 0.7)'
	        });
	        
	        self.axios({
	          method: 'get',
	          url:api.aboutme,
	        }).then(function (res) {       
	            if(res.data.code==0){
	              loading.close();
	              self.data=res.data.data;
	            }else{
	              loading.close();
	                    
	              self.$message({
	                    message:res.data.message,
	                    type: 'warning'
	                  });  
	                 
	            }

	                   
	          }).catch(function (error) {
	            loading.close();
	            self.$message({
	                    message:error,
	                    type: 'warning'
	                  }); 
	                       　　
	          });

	      },
	      gotoDetail(tit,id){
	      	this.$router.push({
	      		name:'addNotice',
	      		query:{
	      			tit:tit,
	      			id:id
	      		}
	      	})
	      }


		}
	}
</script>
<style scoped lang="scss">
#noticeDiv{
	h3{
		text-align:center;
		font-size:20px;
	}
	.time{
		text-align:center;
		color:#999;
		font-size:12px;
		line-height:40px;
	}
	.headBox{
		img{
			width:100%;
		}
	}
	.userInfoBox{
		p{
			line-height:1.5;
			margin-bottom:15px;
		}
	}
}
</style>


